import {$} from "../jquery";
import {Core} from "../di";
import {Logger} from "./Logger";
import {Navigator} from "../navigator";
import {EventEmitter,EventEmitterFactory} from "../utils";
import {PageManager} from "../page"
interface IDevTool{
    name:string;
    expose:Function[];
}
@Core(
    {
        name:"DevTools",
        dependencies:[
            $,
            Logger,
            Navigator,
            EventEmitterFactory,
            PageManager
        ],
        public:true
    }
)
export class DevTools {
    public static readonly NAMESPACE = "devTools";
    public static readonly ON_DEV_MODE_CHANGE = `${DevTools.NAMESPACE}:devModeChange`;
    protected static readonly CLASS_DEV_MODE_ENABLED = "haztivity-dev-mode";
    protected _isEnabled=false;
    protected _currentLoggerLevel:number;
    protected _eventEmitter:EventEmitter;
    /**
     * Tools for development
     */
    constructor(protected _$,
                protected _Logger,
                protected _Navigator,
                protected _EventEmitterFactory,
                protected _PageManager){
        this._eventEmitter = this._EventEmitterFactory.createEmitter();
    }
    /**
     * Enables development mode.
     */
    public enable(){
        if(!this.isEnabled()) {
            this._isEnabled = true;
            this._Navigator.enableDev();
            this._currentLoggerLevel = this._Logger.getLevel();
            this._Logger.setLevel(this._Logger.levels.TRACE);
            this._$(document.body).addClass(DevTools.CLASS_DEV_MODE_ENABLED);
            this._Logger.warn("DevTools", "Development mode enabled. Log level set to TRACE");
            this._eventEmitter.globalEmitter.trigger(DevTools.ON_DEV_MODE_CHANGE,true);
        }
    }

    /**
     * Disables development mode
     */
    public disable(){
        if(this.isEnabled()) {
            this._isEnabled = false;
            this._Navigator.disableDev();
            this._Logger.setLevel(this._currentLoggerLevel);
            this._currentLoggerLevel = null;
            this._$(document.body).removeClass(DevTools.CLASS_DEV_MODE_ENABLED);
            this._Logger.warn("DevTools", "Development mode disabled. Log level restored");
            this._eventEmitter.globalEmitter.trigger(DevTools.ON_DEV_MODE_CHANGE,false);
        }
    }

    /**
     * Return if the development mode is enabled
     * @returns {boolean}
     */
    public isEnabled(){
        return this._isEnabled;
    }
    /**
     * Force to go to the next page
     */
    public goToNextPage(){
        if(this.isEnabled()) {
            this._Navigator.next();
        }
    }

    /**
     * Force to go to the previous page
     */
    public goToPrevPage(){
        if(this.isEnabled()) {
            this._Navigator.prev();
        }
    }
    /**
     * Force to go to a specific page
     * @param index Index of the page
     */
    public goToPage(index){
        if(this.isEnabled()) {
            this._Navigator.goTo(index);
        }
    }

    /**
     * Force to go to a specific page
     * @param name  Name of the page
     */
    public goToPageByName(name){
        if(this.isEnabled()) {
            let index = this._PageManager.getPageIndex(name);
            if(index != -1) {
                this._Navigator.goTo(index);
            }
        }
    }
    /**
     * Get the name of the current page
     * @returns {string}
     */
    public getCurrentPageName(){
        if(this.isEnabled()){
            return this._Navigator.getCurrentPage().getPageName();
        }
    }

    /**
     * Create an event emitter
     */
    public createEventEmitter():EventEmitter{
        if(this.isEnabled()) {
            return this._EventEmitterFactory.create();
        }
    }
}